import * as types from './mutation-types'

const matutaions = {
  [types.SET_UDATA] (state, udata) {
    state.udata = udata
  },
  [types.SET_REGISTERINFO] (state, info) {
    state.registerInfo = info
  },
  [types.SET_CONFIG] (state, config) {
    state.config = config
  }
}

export default matutaions



